html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Comic Sans MS", "Helvetica Neue", "Microsoft Yahei", -apple-system, sans-serif !important;
  font-size: 16px;

  overflow-x: hidden;

  cursor: url("https://cdn.jsdelivr.net/gh/SimonAKing/images/blog/default.cur") , auto !important;

  background: #f1f1f1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
img,
button,
nav,
a,
span {
  cursor: url("https://cdn.jsdelivr.net/gh/SimonAKing/images/blog/pointer.cur") , auto !important;
}

::-webkit-scrollbar {
  width: 0px;
}

#gallery {
  margin-bottom: 40px;
}

.photo {
  position: relative;

  overflow: hidden;

  width: 100%;
  height: 100%;

  cursor: pointer;
  transition: box-shadow 0.3s ease, transform 0.3s ease;

  border-radius: 4px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);

  will-change: auto;
  transform-style: preserve-3d;
}

.photo:hover {
  box-shadow: 0 14px 35px rgba(0, 0, 0, 0.2);
}

.photo:hover span {
  opacity: 1;
}

.photo img {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  transition-timing-function: ease;
  transition-duration: 0.4s;
  transition-property: opacity, transform, filter;
  transform: scale(1.2) translateZ( 20px);

  opacity: 0;

  filter: blur(5px);
  transform-style: preserve-3d;
}

.photo img.loaded {
  transform: scale(1);

  opacity: 1;

  filter: blur(0px);
}

.blurhash-placeholder {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  transition: opacity 0.4s ease;

  opacity: 1;
}

.blurhash-placeholder.fade-out {
  opacity: 0;
}

.photo span {
  font-weight: bold;

  position: absolute;
  z-index: 10;
  bottom: 0;
  left: 0;

  display: flex;
  overflow: hidden;
  flex-shrink: 0;
  flex-wrap: nowrap;

  width: 100%;
  margin-top: auto;
  padding: 8px 12px;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translateY(100%);
  white-space: nowrap;
  letter-spacing: 1px;
  text-overflow: ellipsis;
  pointer-events: none;

  opacity: 0;
  color: white;
  background: linear-gradient(to bottom, transparent 0%, rgba( 0, 0, 0, 0.7) 81%);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);

  backdrop-filter: blur(4px);
}

.photo:hover span {
  transform: translateY(0);

  opacity: 1;
}

.react-images__view img {
  width: auto;
  max-width: 100%;
  max-height: 100vh;
  margin: auto;

  transform: scale(0.9) !important;

  object-fit: contain;
}

@media screen and(max-width: 768px) {
  .react-images__view img {
    transform: scale(1) !important;
  }
}

button:focus {
  outline: 0 !important;
}

footer {
  font-size: 0.9rem;

  position: relative;
  z-index: 2;
  bottom: 0;
  left: 0;

  display: flex;
  clear: both;
  overflow: hidden;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 25px;
  padding: 0 0.4rem;

  user-select: none;
  transition: 0.2s ease-in-out;
  text-align: center;
  letter-spacing: 1px;

  color: #fff;
  border-top: 4px solid #e0e2e6;
  background-image: linear-gradient(-90deg, #f5f1f1, rgb( 199, 199, 199) 20%, rgb( 199, 199, 199) 80%, #f5f1f1);
  text-shadow: 0 0 2px #ccc;

  backface-visibility: hidden;
}

@media screen and(max-width: 768px) {
  footer {
    position: relative;
  }
}

footer a {
  display: inline-block;

  margin-left: 5px;

  text-decoration: none;

  color: #fff;
}

footer a:hover {
  text-decoration: underline;
}

footer span {
  font-size: 1.5rem;
  line-height: 1.9rem;

  display: inline-block;

  height: 100%;
  margin-right: 3px;
}
